  <div>

  <el-card>
    <el-table
    :data="accountList"
    style="width: 100%">
    <el-table-column
      type="index">
    </el-table-column>
    <el-table-column label="success" width="80px">
      <template slot-scope="scope">
        {{ scope.row.success }}
      </template>
    </el-table-column>
    <el-table-column label="交易对">
      <template slot-scope="scope">
        {{ scope.row.conConfig.coinPair }}
      </template>
    </el-table-column>
    <el-table-column label="可用持仓">
      <template slot-scope="scope">
        {{ scope.row.conConfig.coinPair.split('/')[0] }}:{{ scope.row.balance[scope.row.conConfig.coinPair.split('/')[0]] && scope.row.balance[scope.row.conConfig.coinPair.split('/')[0]].free || 0 }}
        <br>
        {{ scope.row.conConfig.coinPair.split('/')[1] }}:{{ scope.row.balance[scope.row.conConfig.coinPair.split('/')[1]] && scope.row.balance[scope.row.conConfig.coinPair.split('/')[1]].free || 0 }}
      </template>
    </el-table-column>
    <el-table-column label="通道">
      <template slot-scope="scope">
        上沿:{{ scope.row.algo.don_open }}<br>
        下沿:{{ scope.row.algo.don_close }}<br>
        ATR:{{ scope.row.algo.atr }}
      </template>
    </el-table-column>
    <el-table-column prop="lastClosePrice" label="最新成交价"></el-table-column>
    <el-table-column prop="unit" label="建仓单元"></el-table-column>
    <el-table-column label="最小开单量">
      <template slot-scope="scope">
        {{ scope.row.symbolLimit.amount.min }}
      </template>
    </el-table-column>
    <el-table-column label="建仓预消耗本金">
      <template slot-scope="scope">
        {{ scope.row.unit * scope.row.open_point }}
      </template>
    </el-table-column>
    <el-table-column label="是否持仓">
      <template slot-scope="scope">
