<script>
export default {
  data () {
    return {
      accountList: []
    }
  },
  created () {
    this.getAccountList()
  },
  methods: {
    async getAccountList() {
      const { data: res } = await this.$http.get('data/strategy', { params: {} })
      if (!res || !res.success) {
        const { data: res } = await this.$http.get('data/strategy', { params: {} })
        if (!res || !res.success) {
          return this.$message.error('获取列表失败')
        }
      }
      this.accountList = res.data
    }
  }
}
</script>

